/* ==============================
   DASHBOARD BASE
   ============================== */

.dashboard {
  min-height: 100vh;
  padding: 30px;
  background: #0f172a;
  color: #e5e7eb;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

/* ==============================
   HEADER
   ============================== */

.header {
  margin-bottom: 30px;
}

.header h2 {
  margin: 0;
  font-size: 28px;
}

.header p {
  color: #9ca3af;
  margin-top: 5px;
}

/* ==============================
   LAYOUT GRID
   ============================== */

.grid {
  display: grid;
  grid-template-columns: 250px 1fr;
  gap: 20px;
}

/* ==============================
   CARD (Glassmorphism)
   ============================== */

.card {
  background: rgba(2, 6, 23, 0.75);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  padding: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.08);
}

.card h3 {
  margin-top: 0;
  margin-bottom: 15px;
}

/* ==============================
   DEVICE LIST
   ============================== */

.device {
  display: flex;
  align-items: center;
  gap: 10px;

  padding: 10px;
  border-radius: 8px;
  cursor: pointer;
  margin-bottom: 8px;
  background: #020617;

  transition: background 0.2s ease, transform 0.2s ease;
}

.device:hover {
  background: #1e293b;
  transform: translateX(2px);
}

.device.active {
  background: #2563eb;
  box-shadow: 0 0 10px rgba(37, 99, 235, 0.6);
}

/* Online indicator dot */
.device .dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #22c55e;
  box-shadow: 0 0 6px #22c55e;
}

/* ==============================
   KPI STATS
   ============================== */

.stats {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 15px;
  margin-bottom: 20px;
}

.stat-card {
  padding: 15px;
  border-radius: 14px;
  color: #ffffff;
  box-shadow: inset 0 0 0 1px rgba(255, 255, 255, 0.08);
  transition: transform 0.2s ease;
}

.stat-card:hover {
  transform: translateY(-3px);
}

.stat-card p {
  margin: 0;
  font-size: 14px;
  opacity: 0.85;
}

.stat-card h1,
.stat-card h2 {
  margin: 8px 0 0;
}

/* KPI colors */
.stat-card.temp {
  background: linear-gradient(135deg, #0ea5e9, #0369a1);
}

.stat-card.humidity {
  background: linear-gradient(135deg, #22c55e, #15803d);
}

.stat-card.time {
  background: linear-gradient(135deg, #6366f1, #4338ca);
}

/* ==============================
   CHART
   ============================== */

.chart-wrapper {
  height: 350px;
  animation: fadeIn 0.6s ease;
}

/* ==============================
   FOOTER
   ============================== */

footer {
  margin-top: 40px;
  color: #64748b;
  font-size: 14px;
  text-align: center;
}

/* ==============================
   ANIMATIONS
   ============================== */

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.stat-card.battery {
  background: linear-gradient(135deg, #f59e0b, #b45309);
}
.device-meta {
  display: flex;
  flex-direction: column;
  font-size: 14px;
}

.device-meta small {
  color: #9ca3af;
}
